import React from 'react';
import {
    LogoPNG,
    TextFontBg,
    FireGIF,
    TwitterSVG,
    TelegramSVG,
    DiscardSVG,
    OpenseaSVG
} from 'src/assets'
import { useIntl } from 'react-intl'
import { openNewPage } from 'src/utils';
import './index.less';

const sourceLink = {
    twitter: {
        link: "https://twitter.com/Tiandi_Bank",
        icon: TwitterSVG,
        text: 'Twitter'
    },
    telegram: {
        link: "https://t.me/Tiandi_Bank_NFT",
        icon: TelegramSVG,
        text: 'Telegram'
    },
    discord: {
        link: "https://discord.gg/FGxsVH9xvZ",
        icon: DiscardSVG,
        text: 'Discord'
    },
    openSea: {
        link: "https://opensea.io/collection/tiandi-bank",
        icon: OpenseaSVG,
        text: 'OpenSea'
    },
}

const Intro: React.FC = () => {
    const {
        messages: {
            webIntroP1,
            webIntroP2
        }
    } = useIntl();

    return (
        <div id="home">
            {/* <div className="intri-bg"></div> */}
            <img src={FireGIF} className="intro-bg" />
            <div className="intro-card-container container">
                <div className="intro-card">
                    <div className=" intro-card-title">
                        {/* <div className="title-font"></div> */}
                        {/* <div className="title-font-bg"></div> */}
                        <img src={LogoPNG} className="title-font" />
                        <img src={TextFontBg} className="title-font-bg" />
                    </div>
                    <div className="intro-card-content">
                        <p>{webIntroP1}</p>
                        <p>{webIntroP2}</p>
                    </div>
                    <div className="intro-card-link">
                        {
                            Object.keys(sourceLink).map(key =>
                                <div
                                    className="website-card"
                                    key={key}
                                    onClick={() => openNewPage(sourceLink[key].link)}
                                >
                                    <div className="card-left">
                                        {sourceLink[key].text}
                                    </div>
                                    <div className="card-right">
                                        <img src={sourceLink[key].icon} />
                                    </div>
                                </div>
                            )
                        }

                    </div>
                </div>
            </div>

        </div>
    )
}

export default Intro;