import React from 'react';
import { useIntl } from 'react-intl';
import { openNewPage } from 'src/utils';
import {
    TwitterWSVG,
    TelegramWSVG,
    DiscardWSVG,
    OpenseaWSVG,
    LogoPNG
} from 'src/assets';
import './index.less';

const sourceLink = [
    {
        link: "https://twitter.com/Tiandi_Bank",
        icon: TwitterWSVG,
    },
    {
        link: "https://t.me/Tiandi_Bank_NFT",
        icon: TelegramWSVG,
    },
    {
        link: "https://discord.gg/FGxsVH9xvZ",
        icon: DiscardWSVG,
    },
    {
        link: "https://opensea.io/collection/tiandi-bank",
        icon: OpenseaWSVG,
    },
]

const Footer: React.FC = () => {
    const {
        messages: {
            footerIintro
        }
    } = useIntl();

    return (
        <div className="footer">
            <div className="container">
                <img src={LogoPNG} alt="" className="footer-logo" />
                <div className="footer-webintro">
                    <span>{footerIintro}</span>
                </div>
                <div className="footer-link">
                    {
                        sourceLink.map((item, index) =>
                            <div
                                key={index}
                                className="link-box"
                                onClick={() => openNewPage(item.link)}
                            >
                                <img src={item.icon} alt="" />
                            </div>
                        )
                    }

                </div>
                <div className="footer-copyright">
                    <span>© 2022 Eternalverse.io. All rights reserved.</span>
                </div>
            </div>
        </div>
    )
}

export default Footer;