import React, { useState, useEffect } from 'react';
import { useSelector, useDispatch } from "react-redux";
import { setLocale } from "src/store/actions";
import { LogoPNG, DrawerSVG, DrawerCloseSVG, FireOpacityPNG } from 'src/assets';
import { LanguageType, RootState, langList, navList } from 'src/types';
import { bodyFixed, bodyLoose, refreshPage, useNavAnch } from 'src/utils';

import './index.less';
import { LangArrowSVG } from 'src/assets';

const Header: React.FC = () => {
    const [current, setCurrent] = useState<string>('home');
    const [anch] = useNavAnch({ threshold: [0.3] });
    const [showDrawer, setShowDrawer] = useState<boolean>(false);
    const [showLang, setShowLang] = useState<boolean>(true);
    const storeLang = useSelector<RootState>(state => state.localeState.locale);
    const dispatch = useDispatch();

    useEffect(() => {
        setCurrent(anch);
    }, [anch]);

    const switchLang = (lang: LanguageType) => {
        dispatch(setLocale(lang));
        setShowDrawer(false);
        bodyLoose();
    }

    const handleAnch = (id: string) => {
        setCurrent(id);
        setShowDrawer(false);
        bodyLoose();
        setBodyScroll(id);
    }

    const handleDrawer = (show: boolean) => {
        if (show) {
            bodyFixed();
            setShowDrawer(true);
            return;
        }

        bodyLoose();
        setShowDrawer(false);
    }

    // 手动设置body滚动高度
    const setBodyScroll = (id: string) => {
        const scrollTop = document.querySelector<HTMLAnchorElement>(`#${id}`).offsetTop;
        const viewWidth = document.documentElement.clientWidth;
        document.documentElement.scrollTop = scrollTop - (120 / 750) * viewWidth;
        // console.log('scrollTop', document.documentElement.scrollTop);
    }

    return (
        <div className="header">
            <div className="container">
                <div className="top-nav">
                    <img src={LogoPNG} alt="" onClick={() => refreshPage()} />
                    <div className="top-nav-right" onClick={() => handleDrawer(true)}>
                        <img src={DrawerSVG} alt="" />
                    </div>
                </div>
            </div>
            {
                showDrawer &&
                <div className="header-drawer">
                    <div className="drawer-bg" />
                    <div className="drawer-close" onClick={() => handleDrawer(false)}>
                        <img src={DrawerCloseSVG} alt="" />
                    </div>
                    <div className='nav-anch'>
                        {
                            Object.keys(navList).map(key =>
                                <div className="nav-anch-container" key={key}>
                                    <div
                                        className={current === key ? 'anch-active' : ''}
                                        onClick={() => handleAnch(key)}
                                    >
                                        {navList[key]}
                                    </div>
                                </div>

                            )
                        }

                        <div className="lang-btn" onClick={() => setShowLang(!showLang)}>
                            <span>LANGUAGE</span>
                            <img className={showLang ? 'lang-arrow-up' : ''} src={LangArrowSVG} alt="" />
                        </div>

                        {
                            showLang &&
                            <>
                                {
                                    Object.keys(langList).map(key =>
                                        <div
                                            key={key}
                                            className={
                                                `lang-item ${key === storeLang ? ' anch-active' : ''}`
                                            }
                                            onClick={() => switchLang(key as LanguageType)}
                                        >
                                            {langList[key]}
                                        </div>
                                    )
                                }
                            </>
                        }
                    </div>
                </div>
            }
        </div>
    )
}

export default Header;