import React from 'react';

import {
    TeamDividerLeft,
    TeamDividerRight,
    TeamAvatarSVG,
    TeamRedHairSVG,
    QueenJPG,
    JadeJPG
} from 'src/assets'

import './index.less';

const Team: React.FC = () => {
    return (
        <div id="team">
            <div className="team-container">
                <div className="team-divider">
                    <img src={TeamDividerLeft} alt="" className="divider-left" />
                    <div className="divider-center">
                        <img src={TeamAvatarSVG} alt="" />
                        <span>TEAM</span>
                    </div>
                    <img src={TeamDividerRight} alt="" className="divider-right" />
                </div>
                <div className="team-avatar">
                    <div className="team-avatar-box avatar-queen">
                        <img src={JadeJPG} alt="" />
                        <div className="team-avatar-desc">
                            <img src={TeamRedHairSVG} className="desc-left" />
                            <div className="desc-right">
                                <span className="desc-title">Jade Emperors</span>
                                <span className="desc-extra">/ Owner</span>
                            </div>
                        </div>
                    </div>
                    <div className="team-avatar-box avatar-jade">
                        <img src={QueenJPG} alt="" />
                        <div className="team-avatar-desc">
                            <img src={TeamRedHairSVG} className="desc-left" />
                            <div className="desc-right">
                                <span className="desc-title">Queen Mother</span>
                                <span className="desc-extra">/ Designer</span>
                            </div>
                        </div>
                    </div>
                </div>

            </div>
        </div>
    )
}

export default Team;