import React, { useState } from 'react';
import { useIntl } from 'react-intl';

import {
    QASVG,
    QADownSVG,
    QAUpSVG,
} from 'src/assets';

import './index.less';

const FAQ: React.FC = () => {
    const [current, setCurrent] = useState<number>(0);
    const {
        messages: {
            QA_Q1,
            QA_A1,
            QA_Q2,
            QA_A2,
            QA_Q3,
            QA_A3,
            QA_Q4,
            QA_A4,
        }
    } = useIntl();

    const list = [
        {
            q: QA_Q1,
            a: QA_A1,
        },
        {
            q: QA_Q2,
            a: QA_A2,
        },
        {
            q: QA_Q3,
            a: QA_A3
        },
        {
            q: QA_Q4,
            a: QA_A4
        }
    ]

    return (
        <div id="FAQ">
            <div className="container">
                <div className="top">
                    <img src={QASVG} alt="" />
                    <span>FAQS</span>
                </div>
                <div className="FAQ-container">
                    {
                        list.map((item, index) =>
                            <div className="FAQ-box" key={index} onClick={() => setCurrent(index)}>
                                <div className="FAQ-col1">
                                    <span>{index + 1}.</span>
                                </div>
                                <div className="FAQ-col2">
                                    <div className="FAQ-col2-Q">{item.q}</div>
                                    <div className={`FAQ-col2-A ${current === index ? 'A-open' : 'A-close'}`}>
                                        {
                                            (item.a as string).split('|').map((answer, index) => <p key={index}>{answer}</p>)
                                        }
                                    </div>
                                </div>
                                <div className="FAQ-col3">
                                    <img src={index === current ? QAUpSVG : QADownSVG} className="FAC-col3-icon" />
                                </div>
                            </div>
                        )
                    }
                </div>
            </div>
        </div>
    )
}

export default FAQ;