import React from 'react';
import { useIntl } from 'react-intl';
import {
    OpenseaSVG,
    BankDivider,
    Photo1,
    Photo2,
    Photo3,
    Photo4,
    Photo5,
    Photo6,
    Photo7,
    Photo8,

} from 'src/assets/index';

import { openNewPage } from 'src/utils';

import './index.less';

const prolist = [
    {
        id: 80000000000000,
        img: Photo1,
        link: 'https://opensea.io/assets/matic/0x2953399124f0cbb46d2cbacd8a89cf0599974963/26488073659394780675315705162574961850158208867920559950525378693994607804516'
    },
    {
        id: 80000000000,
        img: Photo2,
        link: 'https://opensea.io/assets/matic/0x2953399124f0cbb46d2cbacd8a89cf0599974963/26488073659394780675315705162574961850158208867920559950525378695094119432392',
    },
    {
        id: 10000000000,
        img: Photo3,
        link: 'https://opensea.io/assets/matic/0x2953399124f0cbb46d2cbacd8a89cf0599974963/26488073659394780675315705162574961850158208867920559950525378696193631060168',
    },
    {
        id: 2000000000,
        img: Photo4,
        link: 'https://opensea.io/assets/matic/0x2953399124f0cbb46d2cbacd8a89cf0599974963/26488073659394780675315705162574961850158208867920559950525378697293142687944',
    },
    {
        id: 10000000000,
        img: Photo5,
        link: 'https://opensea.io/assets/matic/0x2953399124f0cbb46d2cbacd8a89cf0599974963/26488073659394780675315705162574961850158208867920559950525378698392654315720',
    },
    {
        id: 800000000,
        img: Photo6,
        link: 'https://opensea.io/assets/matic/0x2953399124f0cbb46d2cbacd8a89cf0599974963/26488073659394780675315705162574961850158208867920559950525378699492165943596',
    },
    {
        id: 50000000,
        img: Photo7,
        link: 'https://opensea.io/assets/matic/0x2953399124f0cbb46d2cbacd8a89cf0599974963/26488073659394780675315705162574961850158208867920559950525378700591677571472',
    },
    {
        id: 50000000,
        img: Photo8,
        link: 'https://opensea.io/assets/matic/0x2953399124f0cbb46d2cbacd8a89cf0599974963/26488073659394780675315705162574961850158208867920559950525378701691189199248'
    }
]

const Bank: React.FC = () => {
    const {
        messages: {
            bankIntroP1,
            bankIntroP2,
        }
    } = useIntl();

    return (
        <div id="tiandi" className="container">
            <div className="bank-intro">
                <div className="intro-title">
                    TIANDI BANK NFT
                </div>
                <div className="intro-content">
                    <p>{bankIntroP1}</p>
                    <p>{bankIntroP2}</p>
                </div>
            </div>
            <div className="bank-main">
                <img src={BankDivider} className="bank-divider" />
                <div className="bank-products">
                    {
                        prolist.map((prod, index) =>
                            <div className="prod-card" key={index}>
                                <img src={prod.img} className="prod-img" />
                                <div className="prod-footer">
                                    <div className="prod-left">
                                        <span className="prod-name">Tiandi Bank</span>
                                        <span className="prod-id">{prod.id}</span>
                                    </div>
                                    <div className="prod-right">
                                        <div className="prod-icon" onClick={() => openNewPage(prod.link)}>
                                            <img src={OpenseaSVG} />
                                        </div>
                                    </div>
                                </div>
                            </div>
                        )
                    }
                </div>
            </div>
        </div>
    )
}

export default Bank;