import React from 'react';
import { useIntl } from 'react-intl';

import {
    RoadBoySVG,
    RoadArrowSVG,
    RoadSlashPNG,
    FireOpacityPNG
} from 'src/assets'

import './index.less';

const RoadMap: React.FC = () => {
    const {
        messages: {
            roadQ1,
            roadQ2,
            roadQ3,
            roadQ4,
        }
    } = useIntl();

    const desc = [roadQ1, roadQ2, roadQ3, roadQ4];
    return (
        <div id="roadmap">
            <div className="map-bg">
                <img src={FireOpacityPNG} />
            </div>
            <div className="road-container container">
                <div className="road-title">
                    <img src={RoadBoySVG} />
                    <span>Roadmap - 2022</span>
                </div>
                {
                    !!desc.length && desc.map((foot, index) =>
                        <div className="road-footerprint" key={index}>

                            <div className="footer-col1">
                                <span>Q{index + 1}.</span>
                            </div>
                            <div className="footer-col2">
                                <div className="col2-img-container">
                                    <img src={RoadArrowSVG} alt="" />
                                </div>
                            </div>
                            <div className="footer-col3">
                                {
                                    (foot as string).split('|').map((item, index) =>
                                        <div className="desc-line" key={index}>
                                            <div className="desc-line-img">
                                                <img src={RoadSlashPNG} alt="" />
                                            </div>
                                            <p className="footer-desc">{item}</p>
                                        </div>
                                    )
                                }
                            </div>
                        </div>
                    )
                }
            </div>
        </div>
    )
}

export default RoadMap;